.App {
  color: #000;
  background-color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  line-height: 1.4;
  margin: 0 auto;
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 30px;
  margin-top: 15px;
}

.App-header {
  background-color: #222;
  height: 30px;
  padding: 20px;
  color: white;
}

footer {
  margin: 20px;
  letter-spacing: 2px;
  text-align: center;
  display: block;
}

footer span {
  margin:15px; 
}

footer a:link {
  text-decoration: none;
  color: #180e0a; 

}

footer a:visited {
  text-decoration: none;
  color: #180e0a; 
}

footer a:hover {
  text-decoration: none;
  font-weight: bold;
}
.App-title {
  font-size: 1.5em;
  display: inline;
  vertical-align: top;
}

.App-intro {
  font-size: large;
  margin: 20px;
}

.whatis {
  background: -webkit-linear-gradient(left, #eee, #f9f9f9);
  border: 1px solid #ccc;
  border-radius: 10px;
  margin: 15px;
  padding: 12px
}

.iswhat {
  background: -webkit-linear-gradient(left, #eee, #f9f9f9);
  border: 1px solid #ccc;
  border-radius: 10px;
  margin: 15px;
  padding: 12px
}

.incDec {
  background: -webkit-linear-gradient(left, #eee, #f9f9f9);
  border: 1px solid #ccc;
  border-radius: 10px;
  margin: 15px;
  padding: 12px
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


